* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* globals */
body {
  padding-top: 66px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  line-height: 1.4;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.75rem 2rem;
  background: #fff;
  z-index: 1;
}
.nav-header {
  display: flex;
  justify-content: space-between;
}
.nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.nav-icon {
  font-size: 3rem;
  color: #222;
}

.nav-links {
  height: 0;
  overflow: hidden;
  transition: all 0.3s linear;
  list-style-type: none;
  color: black;
  background-color: #fff;
}
.nav-links a {
  display: block;
  text-decoration: none;
  padding: 1rem 0;
  color: var(--mainBlack);
  transition: var(--mainTransition);
  text-align: center;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: var(--mainSpacing);
}
.nav-links a:hover {
  border: 1px solid black;
  background-color: black;
  color: #fff;
}
.show-nav {
  height: 100px;
}

/* header */

.defaultHero {
  position: relative;
  min-height: 60vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./components/images/Background.jpeg") center/cover no-repeat;
}

.productsHero {
  min-height: 40vh;
  background: url("./components/images/productsHero.jpeg") no-repeat center 60%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero_title {
  border-radius: 10px;
  text-transform: uppercase;
  background: #222;
  opacity: 0.7;
  padding: 4rem;
  font-size: 3rem;
  color: #fff;
}

.logo {
  width: 100px;
  height: auto;
}

/*title */
.defaultBanner {
  position: absolute;
  top: 50%;
  left: 65%;
  text-transform: capitalize;
}

.errorBanner {
  display: inline-block;
  background: rgba(0, 0, 0, 0.5);
  padding: 2rem 1rem;
  color: #fff;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
}

.banner h1 {
  font-size: 2.5rem;
}
.banner div {
  width: 10rem;
  height: 115px;
  margin: 1.7rem auto;
}
.subtitle {
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

/* End of Banner */
/* Title */

.title {
  font-size: 2rem;
  text-transform: capitalize;
  letter-spacing: 0.1rem;
  color: #fff;
}

.subtitle {
  margin-top: 1rem;
  color: #fff;
  font-size: 1rem;
}

.btn-primary {
  background: #fff;
  display: inline-block;
  text-decoration: none;
  cursor: pointer;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  font-weight: bold;
}

.btn-primary:hover {
  background: #222;
  color: #fff;
}

.btn-primary:visited {
  color: inherit;
}

/* trending */

.trending_title {
  padding-left: 3rem;
  padding-top: 12rem;
  font-size: 3rem;
}

.trending_container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}

.image_container {
  padding: 1rem;
  position: relative;
}

.trending_image {
  border-radius: 10px;
  display: inline-block;
  width: 100%;
  width: 1200px;
  height: 400px;
  object-fit: cover;
}

.text {
  position: absolute;
  bottom: 15%;
  left: 5%;
  font-size: 2rem;
  color: #fff;
  text-shadow: 0.5px 0 0.5px #000, -1px 0 1px #000, 0 1px 1px #000,
    0 -1px 1px #000;
}

.text2 {
  position: absolute;
  bottom: 15%;
  left: 5%;
  font-size: 2rem;
  color: #fff;
  text-shadow: 0.5px 0 0.5px #000, -1px 0 1px #000, 0 1px 1px #000,
    0 -1px 1px #000;
}

.btn {
  position: absolute;
  bottom: 5.5%;
  left: 5%;
  border-radius: 0.5rem;
  text-decoration: none;
  padding: 0.5rem 2rem;
  background: #fff;
  color: #222;
}

.btn:hover {
  opacity: 0.7;
}

.btn:active {
  transform: translateY(2px);
}

.btn2 {
  position: absolute;
  bottom: 5.5%;
  left: 5%;
  border-radius: 0.5rem;
  text-decoration: none;
  padding: 0.5rem 2rem;
  background: #fff;
  color: #222;
}

.btn2:hover {
  opacity: 0.7;
}

/* Sneaker */

.sneaker_container {
  position: relative;
  flex-basis: 600px;
}

.sneakerImg {
  padding: 0.5rem;
  display: inline-block;
  width: 100%;
  height: auto;
  object-fit: cover;
  height: 500px;
}

.sneakerImg:hover {
  opacity: 0.1;
}

/* Featured */

.featured_title {
  margin-left: 25rem;
  font-size: 3rem;
  padding-top: 10rem;
}

.featured_container {
  padding-top: 1rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}

.featured_link {
  display: inline-block;
  top: 50%;
  left: 42%;
  position: absolute;
  background: #fff;
  text-decoration: none;
  cursor: pointer;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  font-weight: bold;
  text-transform: uppercase;
  transform: scale(0);
  transition: all 1 linear;
}

.featured_link:visited {
  color: inherit;
}

.img_container {
  position: relative;
  height: auto;
  flex-basis: 500px;
  height: 500px;
}

.img_container:hover {
  opacity: 0.7;
  background: rgba(0, 0, 0, 0.8);
}

.img_container:hover img {
  opacity: 0.5;
}

.img_container:hover .featured_link {
  transform: scale(1);
}

/* single sneaker item page */

.sneaker_wrap_container {
  margin: 0 auto;
  display: flex;
  height: 600px;
  width: 900px;
}

.sneaker_images_container {
  display: flex;
  flex-flow: row wrap;
  height: 600px;
  flex-basis: 800px;
}

.single_sneaker_img {
  padding: 0.5rem;
  flex: 1 1 300px;
  display: inline-block;
  max-width: 100%;
  height: auto;
}

.single_sneaker_info {
  padding: 1rem;
  padding-top: 6rem;
  flex-basis: 300px;
  height: 200px;
  flex-direction: row;
}

.sneaker_button {
  margin-top: 1rem;
  width: 100%;
  text-decoration: none;
  background: #222;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 20px;
}

.like_button {
  margin-top: 0.5rem;
  width: 100%;
  text-decoration: none;
  border: 1px solid black;
  background: #fff;
  padding: 1rem 2rem;
  border-radius: 20px;
}

.description {
  font-size: 1rem;
}

.sneaker_info {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

/* sneaker list in filtered page */

.sneakerList {
  margin: 0 auto;
  display: flex;
  height: 1800px;
  width: 1800px;
  flex-flow: row wrap;
}

/* filter form */

.filter_container {
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60vw;
  padding: 5rem 0;
  display: flex;
}
.form_container {
  display: flex;
  flex-flow: row;
}
.form_group {
  padding: 2rem;
  text-transform: capitalize;
}
.form_group label {
  display: block;
  letter-spacing: 0.1rem;
  margin-bottom: 0.5rem;
}
.form_control {
  width: 100%;
  background: transparent;
  font-size: 1rem;
}

/* banner */

.banner {
  min-height: 100px;
  background: #222;
  padding: 2rem;
}

.banner_wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.banner_title {
  text-align: center;
  color: #f3f3f3;
  font-size: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

.banner_button {
  text-transform: uppercase;
  cursor: pointer;
  text-decoration: none;
  margin-left: 1rem;
  border-radius: 5px;
  background-color: #fff;
  font-weight: bold;
  color: #222;
  padding: 1rem 1rem;
}

/* footer */

.footer_menu {
  padding-top: 4rem;
  background: #000;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.section_col {
  color: #f3f3f3;
  font-size: 14px;
}

.item_info {
  text-decoration: none;
  margin-top: 0.5rem;
  display: block;
  color: #4a4a4a;
  font-size: 12px;
  width: 100%;
}

.item_info:hover {
  color: #f3f3f3;
}

.footer_column {
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  flex: 0 0 10%;
}

@media only screen and (max-width: 600px) {
  .footer_column {
    flex: 0 0 33%;
  }
}

